<template>
    <div class="home">
        <Layout>
            <Content>
              <Form ref="checkData" :model="checkData" inline>
                <FormItem prop="red">
                  <Input type="text" v-model="checkData.red" placeholder="红球">
                  </Input>
                </FormItem>
                <br/>
                <FormItem prop="blue">
                  <Input type="text" v-model="checkData.blue" placeholder="蓝球">
                  </Input>
                </FormItem>
                <br/>
                <FormItem>
                  <Button type="primary" @click="check('checkData')">查询</Button>
                </FormItem>
              </Form>
                <List border item-layout="vertical">
                    {{title}}
                    <ListItem v-for="(info, index) in lottery" :key="index">
                      <li>期号：{{info.code}}</li>
                      <li>日期：{{info.date}}</li>
                      <li>当期销量: {{info.sales}}</li>
                      <li>奖池: {{info.poolmoney}}</li>
                      <li>一等奖分布：{{info.content}}</li>
                    </ListItem>
                </List>
            </Content>
        </Layout>
    </div>
</template>

<script>
    export default {
        name: "Home",
        data () {
            return {
                checkData: {
                    red: '',
                    blue: '',
                },
                lottery: [],
                title: ''
            }
        },
        methods: {
            check() {
                this.axios
                    .get('/api/check', {
                        params: {
                            red: this.checkData.red,
                            blue: this.checkData.blue
                        }
                    })
                    .then(response => {
                        if (response.data.code === 200) {
                            this.lottery = response.data.list;
                            if (response.data.list.length > 0) {
                                this.title = '有历史中奖号(自2013年1月1号开始)';
                            } else {
                                this.title = '没有历史中奖号(自2013年1月1号开始)';
                            }
                        }
                    })
                    .catch(error => {
                        console.log(error);
                    })
            }
        }
    }
</script>

<style scoped>
    .home {
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
    }
</style>



